"use client";
import { useState, useEffect } from "react";
import { CheckCircle, RefreshCw, ArrowLeft, Trophy } from "lucide-react";

const API_URL = "http://localhost:8080/api";

type MatchedOrder = {
  id: number;
  seller_price: number;
  buyer_price: number;
  seller_qty: number;
  buyer_qty: number;
  seller_time: string;
  buyer_time: string;
  seller_date: string;
  buyer_date: string;
  incoming_time: string;
  outgoing_time: string;
  time_taken: string;
  status: string;
  transaction_type: number;
};

export default function MatchedOrdersPage() {
  const [data, setData] = useState<MatchedOrder[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const getTransactionTypeName = (type: number) => {
    if (type === 0) return "Cash";
    if (type === 1) return "Crypto";
    if (type === 2) return "Both";
    return "Unknown";
  };

  const formatTime = (timeStr: string) => {
    try {
      const date = new Date(timeStr);
      return date.toLocaleTimeString('en-US', { 
        hour: '2-digit', 
        minute: '2-digit',
        hour12: true 
      });
    } catch {
      return timeStr;
    }
  };

  const formatDate = (dateStr: string) => {
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('en-US', { 
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      });
    } catch {
      return dateStr;
    }
  };

  const fetchMatchedOrders = async () => {
    setLoading(true);
    setError(null);
    
    try {
      const response = await fetch(`${API_URL}/matched-orders`);
      if (response.ok) {
        const result = await response.json();
        setData(result || []);
      } else {
        setError(`Failed to fetch data: ${response.statusText}`);
      }
    } catch (err) {
      setError("Network error. Please check if backend is running.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchMatchedOrders();
    
    // Auto-refresh every 3 seconds
    const interval = setInterval(fetchMatchedOrders, 3000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-6 flex items-center justify-between">
          <button
            onClick={() => window.history.back()}
            className="flex items-center gap-2 px-4 py-2 bg-white border-2 border-black rounded-lg hover:bg-gray-100 transition-all"
          >
            <ArrowLeft size={20} />
            <span className="font-semibold">Back</span>
          </button>
          
          <button
            onClick={fetchMatchedOrders}
            disabled={loading}
            className="flex items-center gap-2 px-4 py-2 bg-black text-white rounded-lg hover:bg-gray-800 transition-all disabled:opacity-50"
          >
            <RefreshCw size={20} className={loading ? "animate-spin" : ""} />
            <span className="font-semibold">Refresh</span>
          </button>
        </div>

        <div className="bg-white rounded-3xl shadow-lg border-2 border-green-500 bg-gradient-to-br from-green-50 to-white p-8">
          <div className="flex items-center gap-3 mb-6">
            <CheckCircle className="text-green-600" size={40} />
            <h1 className="text-3xl font-bold text-black">Matched Orders (Closed Trades)</h1>
            <span className="ml-auto px-3 py-1 bg-green-400 text-black text-xs font-bold rounded-full">
              AUTO-UPDATING
            </span>
          </div>

          {loading && (
            <div className="text-center py-12">
              <RefreshCw className="animate-spin mx-auto mb-4 text-gray-400" size={48} />
              <p className="text-gray-600">Loading matched orders...</p>
            </div>
          )}

          {error && (
            <div className="bg-red-50 border-2 border-red-200 rounded-lg p-6 text-center">
              <p className="text-red-600 font-semibold">{error}</p>
            </div>
          )}

          {!loading && !error && data.length === 0 && (
            <div className="bg-gray-50 border-2 border-gray-200 rounded-lg p-12 text-center">
              <Trophy className="mx-auto mb-4 text-gray-300" size={64} />
              <p className="text-gray-600 text-lg font-semibold">No matched orders yet</p>
              <p className="text-gray-500 text-sm mt-2">Matches will appear here when buyer and seller prices align</p>
            </div>
          )}

          {!loading && !error && data.length > 0 && (
            <div className="overflow-x-auto">
              <table className="w-full border-collapse">
                <thead>
                  <tr className="border-b-2 bg-green-100 border-green-500">
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">ID</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller's Price</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer's Price</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller's Qty</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer's Qty</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller's Time</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer's Time</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller's Date</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer's Date</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Incoming Time</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Outgoing Time</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Time Taken</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((row, idx) => (
                    <tr
                      key={row.id}
                      className={`border-b border-gray-200 hover:bg-green-100 transition-colors cursor-pointer ${
                        idx % 2 === 0 ? "bg-white" : "bg-green-50"
                      }`}
                    >
                      <td className="px-4 py-3 text-sm text-gray-800 font-semibold">{String(row.id).padStart(3, '0')}</td>
                      <td className="px-4 py-3 text-sm font-bold text-green-600">${row.seller_price.toFixed(2)}</td>
                      <td className="px-4 py-3 text-sm font-bold text-blue-600">${row.buyer_price.toFixed(2)}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{row.seller_qty}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{row.buyer_qty}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{row.seller_time}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{row.buyer_time}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{formatDate(row.seller_date)}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{formatDate(row.buyer_date)}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{formatTime(row.incoming_time)}</td>
                      <td className="px-4 py-3 text-sm text-gray-800">{formatTime(row.outgoing_time)}</td>
                      <td className="px-4 py-3 text-sm text-gray-800 font-semibold">{row.time_taken}</td>
                      <td className="px-4 py-3 text-sm">
                        <span className="px-2 py-1 bg-green-500 text-white text-xs font-bold rounded-full">
                          {row.status}
                        </span>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}

          {!loading && !error && data.length > 0 && (
            <div className="mt-6 text-center text-gray-600 text-sm">
              Showing {data.length} matched {data.length === 1 ? 'order' : 'orders'}
              <span className="ml-2 text-green-600 font-semibold">
                (Auto-refreshes every 3 seconds)
              </span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}